<template>
    <div class="oprator">
        <div class="oprator_filter">
            <span class="oprator_title">商品</span>
            <el-select v-model="value2" placeholder="" size="mini" @change="selectSecond1" style="width:120px">
                <el-option
                        v-for="(item, index) in productList"
                        :key="index"
                        :label="item.ID +'/'+ item.Name"
                        :value="item.ID +'/'+ item.Name">
                </el-option>
            </el-select>
            <span class="oprator_title">支付方式</span>
            <el-select v-model="value3" placeholder="" size="mini" @change="selectSecond2" style="width:85px">
                <el-option
                        v-for="item in options3"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <span class="oprator_title">站点</span>
            <el-select v-model="value4" placeholder="" size="mini" @change="selectSecond3" style="width:75px">
                <el-option
                        v-for="item in options4"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>

            <el-select v-model="value5" placeholder="" size="mini" @change="selectSecond5" style="margin-left: 20px;width:100px">
                <el-option
                        v-for="item in options5"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <el-date-picker
                    v-model="valuetime1"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    size="mini"
                    value-format="timestamp">
            </el-date-picker>
            <el-button type="primary" size="mini" @click="filterBtn">过滤</el-button>
        </div>
        <div class="oprator_search">
            <el-select v-model="valueSearch" placeholder="" size="mini" @change="searchCondition" style="width:110px">
                <el-option
                        v-for="item in optionsSearch"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <el-input
                    placeholder="请输入搜索方式"
                    v-model="input1"
                    clearable
                    size="small"
                    class="search_input">
            </el-input>
            <el-button type="primary" @click="searchBtn()" size="mini">搜索</el-button>
        </div>
        <!-- 待发货全选导出操作 -->
        <div v-if="value1 == 'UNDELIVERED1' || value1 == 'UNDELIVERED2'" class="oprator_derive" style="padding: 20px 0px 0px 40px;">
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
            <el-button type="primary" size="mini" style="margin-left: 20px;" @click="exportOrderConfirm" v-loading.fullscreen.lock="fullscreenLoading">批量导出</el-button>
            <el-checkbox-group v-model="checkedItems" @change="handleCheckedItemsChange" style="z-index:2000; position: relative;">
                <el-checkbox v-for="(item, index) in orderList" :label="item" :key="index" :class="'check'+item.OrderId" :style="'margin-left: 30px;position: absolute;left: -25px;top:'+ (index*216+33)+'px'">{{''}}</el-checkbox>
            </el-checkbox-group>
        </div>
        <!-- 批量发货按钮 -->
        <router-link v-if="value1 == 'UNDELIVERED3'" :to="{path:'/ordermanage/batch_deliver'}" style="position: relative;top:12px;left:53px">
            <el-button type="primary" size="mini">批量发货</el-button>
        </router-link>
        <!-- 批量发货按钮 -->
        <router-link v-if="value1 == 'UNDELIVERED4'" :to="{path:'/ordermanage/batch_deploy'}" style="position: relative;top:12px;left:60px">
            <el-button type="primary" size="mini">批量部署</el-button>
        </router-link>
    </div>
</template>
<script>
    export default {
        name: 'orderFilter',
        data(){
            return {
                orderList:[],
                value1:'',
                value2: '全部', // 商品
                value3: '', // 支付方式
                value4: '', // 站点
                value5: '', // 时间方式（创建时间/更新时间）
                valuetime1: '', // 时间vm
                input1: '', // 搜索框vm
                selectSearch: '请选择',
                fullscreenLoading: false,
                isIndeterminate: true,
                checkAll: false,
                checkedItems: [],
                valueSearch: '请选择',
                productList: [], // 商品下拉框
                options3: [{ // 支付方式下拉框
                    value: '',
                    label: '全部'
                }, {
                    value: 'UnionPay',
                    label: '人民币'
                },{
                    value: 'BTC',
                    label: 'BTC'
                }],
                options4: [{ // 语种选择下拉框
                    value: '',
                    label: '全部'
                }, {
                    value: 'cn',
                    label: '中文'
                },{
                    value: 'en',
                    label: '英文'
                }],
                options5: [{ // 时间多选插件
                    value: '',
                    label: '请选择'
                }, {
                    value: 'createTime',
                    label: '创建时间'
                },{
                    value: 'updateTime',
                    label: '更新时间'
                }],
                optionsSearch: [{
                    value: 1,
                    label: '订单号'
                },{
                    value: 2,
                    label: '用户名'
                },
                    {
                        value: 3,
                        label: '邮箱'
                    },
                    {
                        value: 4,
                        label: '物流单号'
                    },
                    {
                        value: 5,
                        label: '收件人姓名'
                    },{
                        value: 6,
                        label: '托管人姓名'
                    }, {
                        value: 7,
                        label: '订单ID'
                    }]
            }
        },
        watch: {
            value1: { // 深度监听过滤条件变化，并刷新页面
                handler(val, oldVal){
                    // console.log(val, oldVal)
                },
                deep:true
            },
            orderList: { // 深度监听过滤条件变化，并刷新页面
                handler(val, oldVal){
                    // console.log(val, oldVal)
                },
                deep:true
            }
        },
        mounted(){
            this.msg.$on('sendOrderList',(res)=>{
                this.value1 = res.value1
            this.orderList = res.orderList
            // console.log(res)
        })
        },
        methods: {
            selectSecond1(){ // 商品下拉框
                console.log('selectSecond1')
            },
            selectSecond2(){ // 支付方式下拉框
                console.log('selectSecond2')
            },
            selectSecond3(){ // 站点下拉框
                console.log('selectSecond3')
            },
            selectSecond5(){ // 时间方式下拉框
                console.log(this.value5)
            },
            filterBtn(){ // 过滤按钮回调
                // 重置search条件
                this.valueSearch = '请选择'
                this.input1 = ''
                this.msg.$emit('searchBtn',{
                    orderNo: '',
                    account: '',
                    email: '',
                    expressOrderNumber: '',
                    receiverName: '',
                    hostName: '',
                    orderId: ''
                });
                this.updateStartTime = ''
                this.updateEndTime = ''
                this.createStartTime = ''
                this.createEndTime = ''
                if(this.value5 == 'createTime'){
                    this.createStartTime = this.valuetime1[0]/1000
                    this.createEndTime = this.valuetime1[1]/1000
                }else if(this.value5 == 'updateTime'){
                    this.updateStartTime = this.valuetime1[0]/1000
                    this.updateEndTime = this.valuetime1[1]/1000
                }
                let filterMsg = {
                    value2: this.value2,
                    value3: this.value3,
                    value4: this.value4,
                    value5: this.value5,
                    updateStartTime: this.updateStartTime,
                    updateEndTime: this.updateEndTime,
                    createStartTime: this.createStartTime,
                    createEndTime: this.createEndTime
                }
                this.msg.$emit('filterBtn',filterMsg);
            },
            searchCondition(){

            },
            searchBtn(){ // search按钮回调
                // 重置filter条件
                this.value2 = '' // 商品
                this.value3 = '' // 支付方式
                this.value4 = '' // 站点
                this.value5 = '' // 时间方式（创建时间/更新时间）
                this.valuetime1= '' // 时间vm
                this.msg.$emit('filterBtn',{
                    value2: '',
                    value3: '',
                    value4: '',
                    value5: '',
                    updateStartTime: '',
                    updateEndTime: '',
                    createStartTime: '',
                    createEndTime: ''
                })
                if(this.valueSearch == '请选择'){
                    return
                }
                this.orderNo = ''
                this.account = ''
                this.email = ''
                this.expressOrderNumber = ''
                this.receiverName = ''
                this.hostName = ''
                this.orderId = ''
                if(this.valueSearch == 1){
                    this.orderNo = this.input1
                }else if(this.valueSearch == 2){
                    this.account = this.input1
                }else if(this.valueSearch == 3){
                    this.email = this.input1
                }else if(this.valueSearch == 4){
                    this.expressOrderNumber = this.input1
                }else if(this.valueSearch == 5){
                    this.receiverName = this.input1
                }else if(this.valueSearch == 6){
                    this.hostName = this.input1
                }else if(this.valueSearch == 7){
                    this.orderId = this.input1
                }
                let searchMsg = {
                    orderNo: this.orderNo,
                    account: this.account,
                    email: this.email,
                    expressOrderNumber: this.expressOrderNumber,
                    receiverName: this.receiverName,
                    hostName: this.hostName,
                    orderId: this.orderId
                }
                this.msg.$emit('searchBtn',searchMsg);
            },
            handleCheckAllChange(val){ // 全选回调
                this.checkedItems = val ? this.orderList : [];
                this.isIndeterminate = false;
            },
            handleCheckedItemsChange(value){
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.orderList.length;
                this.isIndeterminate = checkedCount > 0 && checkedCount < this.orderList.length;
            },
            exportOrderConfirm(){ // 批量导出
                this.$confirm('确定批量导出订单？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    // console.log(this.checkedItems)
                    if(this.checkedItems < 1){
                    this.$message({
                        type: 'info',
                        message: '请选择需导出的订单!'
                    });
                    return
                }
                this.fullscreenLoading = true
                this.exportOrder()
            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消'
                });
            });
            },
            exportOrder(){
                // console.log(this.checkedItems)
                console.log(this.value1)
                let isHostingOrder = '0'
                if(this.value1 == 'UNDELIVERED2'){
                    isHostingOrder = '1'
                }else{
                    isHostingOrder = '0'
                }
                // 组装导出所需数据格式，例{"orderIds":[75,76,78,79]}
                let idArr = []
                let idObj = {}
                this.checkedItems.forEach((item, index) => {
                    // 获取Id
                    idArr.push(item.Id)
            })
                idObj.orderIds = idArr
                this.$api('/admin/order/export_order', {
                    isHostingOrder:isHostingOrder,
                    orderIds: JSON.stringify(idObj), // 将对象转成json字符串传给接口
                    notice: '批量导出成功'
                }).then(res => {
                    //  console.log('http://47.75.49.33:8087'+res.data.csvFilePath)
                    // 打开url完成下载
                    location.href = res.data.csvFilePath
                this.fullscreenLoading = false
                // this.getOrderList(this.value1)
                // 将导出订单传出父组件
                let checkedItems = this.checkedItems
                this.msg.$emit('exportChecked',checkedItems);
                this.checkedItems = []
                // setTimeout('window.location.reload()', 1500)
            });
            },
            getProductList(){
                this.$api('/admin/product/list', {
                    status: '1,2,3,5',
                    pageNo: 0,
                    pageSize: 100
                }).then(res => {
                    // console.log(res)
                    let d = res.data
                    // console.log(d)
                this.productList = d.list
                // this.productList.forEach((item, index) => {
                //     if(item.DefaultAttr.InStock <= 0){
                //         this.productList.splice(index, 1)
                //     }
                // })
            });
            }
        },
        created(){
            this.getProductList()
        }
    }
</script>
<style lang='scss' scoped>
    @import "../assets/css/titlecom.scss";
</style>


