<template>
    <div class="all_com">
        <div class="title_com">
            <span>订单列表</span>
            <el-select
                    v-model="value1"
                    placeholder="请选择"
                    @change="selectStair">
                <el-option
                        v-for="item in options1"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="exportList" style="margin:10px 0 0 20px;">
            <span>共{{this.totalNo}}个</span>
            <el-button size="mini" type="text" @click="exportList">导出</el-button>
        </div>
        <!-- 过滤条件 -->
        <orderFilter>

        </orderFilter>
        <!-- 订单列表公共组件 -->
        <orderComponent
                :totalNo = "totalNo"
                :isOffine = "isOffine">
        </orderComponent>
    </div>
</template>
<script>
    import orderComponent from '../../components/orderComponent.vue'
    import orderFilter from '../../components/orderFilter.vue'
    export default {
        data() {
            return {
                options1: [{
                    value: 'ALL_ORDER',
                    label: '订单列表'
                }, {
                    value: 'UNPAID',
                    label: '待确认支付'
                }, {
                    value: 'PRESELL_PAID',
                    label: '已支付订金'
                }, {
                    value: 'BEGIN_FINAL_PAY',
                    label: '开始支付尾款'
                }, {
                    value: 'WAIT_FOR_FINAL_PAY',
                    label: '待支付尾款'
                }, {
                    value: 'UNDELIVERED1',
                    label: '待发货（发货）'
                },{
                    value: 'UNDELIVERED2',
                    label: '待发货（托管）'
                }, {
                    value: 'UNDELIVERED3',
                    label: '已安排发货'
                }, {
                    value: 'UNDELIVERED4',
                    label: '已安排部署'
                }, {
                    value: 'DELIVERED1',
                    label: '已发货（发货）'
                }, {
                    value: 'DELIVERED2',
                    label: '已发货（托管）'
                }, {
                    value: 'COMPLETED1',
                    label: '确认收货'
                }, {
                    value: 'COMPLETED2',
                    label: '完成部署'
                }, {
                    value: 'CANCELED',
                    label: '已取消'
                },{
                    value: 'EXPIRED',
                    label: '已过期'
                }],
                value1: 'ALL_ORDER',
                orderList: [],
                IsHostingOrder: '',
                ReadyDeliver: '',
                totalNo: 0,
                pageNo: 1,
                pageSize:50,
                isOffine: false, // 是否为线下订单参数
                filterMsg: {},
                searchMsg: {},
                IsHostingOrderExport: '',
                ReadyDeliverExport: '',
                orderListExport:[]
            }
        },
        components: {
            orderComponent,
            orderFilter
        },
        methods: {
            exportList(){
                this.IsHostingOrderExport = ''
                this.ReadyDeliverExport = ''
                let status = this.value1
                // 若this.value1为空，后端不判断addressId
                if(this.value1 == 'UNDELIVERED1'){
                    status = 'UNDELIVERED'
                    // IsHostingOrder传0，为待发货（发货）
                    this.IsHostingOrderExport = '0'
                    this.ReadyDeliverExport = '0'
                }else if(this.value1 == 'UNDELIVERED2'){
                    status = 'UNDELIVERED'
                    // IsHostingOrder传1，为待发货（托管）
                    this.IsHostingOrderExport = '1'
                    this.ReadyDeliverExport = '0'
                }else if(this.value1 == 'UNDELIVERED3'){
                    status = 'UNDELIVERED'
                    // ReadyDeliver=1为已安排发货，0为待发货
                    this.ReadyDeliverExport = '1'
                    this.IsHostingOrderExport = ''
                }else if(this.value1 == 'DELIVERED1'){
                    // 已发货需判断是否托管
                    status = 'DELIVERED'
                    this.IsHostingOrderExport = '0'
                }else if(this.value1 == 'DELIVERED2'){
                    // 已发货需判断是否托管
                    status = 'DELIVERED'
                    this.IsHostingOrderExport = '1'
                }
                // console.log(status)
                // if(this.filterMsg.value2){
                //     this.productName = this.filterMsg.value2.split('/')[1]
                // }else{
                //     this.productName = ''
                // }
                let param = {
                    productName: this.productName,
                    payment: this.filterMsg.value3,
                    lang: this.filterMsg.value4,
                    createStartTime: this.filterMsg.createStartTime,
                    createEndTime: this.filterMsg.createEndTime,
                    updateStartTime: this.filterMsg.updateStartTime,
                    updateEndTime: this.filterMsg.updateEndTime,
                    readyDeliver: this.ReadyDeliverExport,
                    isHostingOrder: this.IsHostingOrderExport,
                    status: status == 'ALL_ORDER'? '':status,
                    orderNo: this.searchMsg.orderNo,
                    account: this.searchMsg.account,
                    email: this.searchMsg.email,
                    expressOrderNumber: this.searchMsg.expressOrderNumber,
                    receiverName: this.searchMsg.receiverName,
                    hostName: this.searchMsg.hostName,
                    orderId: this.searchMsg.orderId,
                    pageNo: '',
                    pageSize:''
                }
                this.$api('/admin/order/get_order_list', param).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.orderListExport = d.orderList
                console.log(this.orderListExport)
                let exportList =[]
                this.orderListExport.forEach((item, index) => {
                    exportList.push(item.Id)
            })
                let exportListObj = {}
                this.$set(exportListObj,'orderIds',exportList)
                this.$confirm('是否确认导出?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    // console.log(JSON.stringify(exportListObj))
                    this.$api('/admin/order/export_order_total', {
                    orderIds: JSON.stringify(exportListObj)
                }).then(res => {
                    // console.log(res)
                    // console.log(this.orderList)
                    location.href = res.data.csvFilePath
                // setTimeout('location.reload()', 1000)
                // console.log(this.totalNo)
            });
            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '取消操作'
                });
            });
            })
                return
            },
            selectStair(){
                // console.log(this.value1)
                this.getOrderList(this.value1)
            },
            getOrderList(status){
                this.IsHostingOrder = ''
                this.ReadyDeliver = ''
                // 若this.value1为空，后端不判断addressId
                if(status == 'UNDELIVERED1'){
                    status = 'UNDELIVERED'
                    // IsHostingOrder传0，为待发货（发货）
                    this.IsHostingOrder = '0'
                    this.ReadyDeliver = '0'
                }else if(status == 'UNDELIVERED2'){
                    status = 'UNDELIVERED'
                    // IsHostingOrder传1，为待发货（托管）
                    this.IsHostingOrder = '1'
                    this.ReadyDeliver = '0'
                }else if(status == 'UNDELIVERED3'){
                    status = 'UNDELIVERED'
                    // ReadyDeliver=1为已安排发货，0为待发货
                    this.ReadyDeliver = '1'

                    this.IsHostingOrder = '0'
                }else if(status == 'UNDELIVERED4'){
                    status = 'UNDELIVERED'
                    // ReadyDeliver=1为已安排发货，0为待发货
                    this.ReadyDeliver = '1'

                    this.IsHostingOrder = '1'
                }else if(status == 'DELIVERED1'){
                    // 已发货需判断是否托管
                    status = 'DELIVERED'
                    this.IsHostingOrder = '0'
                }else if(status == 'DELIVERED2'){
                    // 已发货需判断是否托管
                    status = 'DELIVERED'
                    this.IsHostingOrder = '1'
                }
                if(status == 'COMPLETED1'){
                    status = 'COMPLETED'
                    this.IsHostingOrder = '0'
                }else if(status == 'COMPLETED2'){
                    status = 'COMPLETED'
                    this.IsHostingOrder = '1'
                }
                // console.log(this.IsHostingOrder)
                if(this.filterMsg.value2){
                    this.productName = this.filterMsg.value2.split('/')[1]
                }else{
                    this.productName = ''
                }
                let param = {
                    productName: this.productName,
                    payment: this.filterMsg.value3,
                    lang: this.filterMsg.value4,
                    createStartTime: this.filterMsg.createStartTime,
                    createEndTime: this.filterMsg.createEndTime,
                    updateStartTime: this.filterMsg.updateStartTime,
                    updateEndTime: this.filterMsg.updateEndTime,
                    readyDeliver: this.ReadyDeliver,
                    isHostingOrder: this.IsHostingOrder,
                    status: status == 'ALL_ORDER'? '':status,
                    orderNo: this.searchMsg.orderNo,
                    account: this.searchMsg.account,
                    email: this.searchMsg.email,
                    expressOrderNumber: this.searchMsg.expressOrderNumber,
                    receiverName: this.searchMsg.receiverName,
                    hostName: this.searchMsg.hostName,
                    orderId: this.searchMsg.orderId,
                    pageNo: this.pageNo,
                    pageSize:this.pageSize
                }
                this.$api('/admin/order/get_order_list', param).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.orderList = d.orderList
                this.totalNo = d.page.totalNum
                let orderListMsg = {
                    value1:this.value1,
                    orderList: this.orderList
                }
                this.msg.$emit('sendOrderList',orderListMsg);
            })
            }
        },
        watch: {
            filterMsg: { // 深度监听过滤条件变化，并刷新页面
                handler(val, oldVal){
                    // console.log(val, oldVal)
                    this.getOrderList(this.value1)
                },
                deep:true
            },
            searchMsg: { // 深度监听搜索条件变化，并刷新页面
                handler(val, oldVal){
                    // console.log(val, oldVal)
                    this.getOrderList(this.value1)
                },
                deep:true
            },
            pageNo: { // 深度监听分页条件变化，并刷新页面
                handler(val, oldVal){
                    this.getOrderList(this.value1)
                },
                deep:true
            }
        },
        mounted(){
            // 拿到过滤条件
            this.msg.$on('filterBtn',(res)=>{
                console.log(res)
            this.filterMsg = res
        })
            // 拿到搜索条件
            this.msg.$on('searchBtn',(res1)=>{
                // console.log(res1)
                this.searchMsg = res1
        })
            // 拿到搜索条件
            this.msg.$on('exportChecked',(res2)=>{
                // console.log(res2)
                let exportChecked = res2
                if(exportChecked.length > 0){ // 若导出订单数>0，则刷新页面
                this.getOrderList(this.value1)
            }
        })
            // 拿到分页条件
            this.msg.$on('paging',(res)=>{
                this.pageNo = res
        })
        },
        created(){
            this.getOrderList('')
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";

</style>


