<template>
    <div class="order_list">
        <div class="order_show">
            <el-card class="box-card-order" v-for="(item, index) in orderList" :key="index" :body-style="{'background-color': '#ffffff'}">
                <div slot="header" class="clearfix order_show_head">
                    <span style="margin:0 0 0 15px">订单号：{{item.OrderId}}</span>
                    <span style="margin:0 20px">下单时间：{{item.CreateTime | date}}</span>
                    <span>更新时间：{{item.UpdateTime | date}}</span>
                    <span v-if="isOffine" style="margin:0 0 0 15px">（线下订单）</span>
                    <el-popover
                            placement="bottom"
                            trigger="click"
                            @show="itemShow(item)"
                            style="float:right;height:19px">
                        <div v-if="(item.Status == 'UNPAID' || item.Status == 'WAIT_FOR_FINAL_PAY') && (item.Payment == 'UnionPay' || item.IsOffline == true || item.PresellPayment == 'UnionPay')" class="order_show_selectitem">
                            <span style="display:inline-block;width:100%;height:100%;" @click="confirmM(item.Id, 'confirmGatherMoney')">确认收款</span>
                        </div>
                        <div v-if="item.Status == 'PRESELL_PAID' || item.Status == 'UNPAID' || item.Status == 'PRESELL_PAID' || item.Status == 'BEGIN_FINAL_PAY' || item.Status == 'WAIT_FOR_FINAL_PAY'"
                             @click="updateOrderPrice(item)"
                             class="order_show_selectitem">修改价格</div>
                        <div v-if="item.Status == 'UNPAID'" class="order_show_selectitem" @click="confirmM(item.Id, 'cancelOrder')">取消订单</div>
                        <!-- 新矿工体验订单无延长时间功能，只能重新下单 -->
                        <div v-if="(item.Status == 'UNPAID' || item.Status == 'EXPIRED') && overTime" class="order_show_selectitem" @click="confirmM(item.Id, 'overtime')">延长时间</div>
                        <div v-if="item.Status == 'UNDELIVERED'" class="order_show_selectitem">
                            <router-link :to="{path:'/ordermanage/order_sending',query:{OrderId:item.Id}}" style="display: inline-block;width:100%;height: 100%;">
                                发货
                            </router-link>
                        </div>
                        <div v-if="item.Status == 'UNDELIVERED' && item.IsHostingOrder == false" class="order_show_selectitem"
                             @click="updateAddress(item.Id)">修改地址</div>
                        <div v-if="item.Status == 'DELIVERED' && item.IsHostingOrder == false" class="order_show_selectitem"
                             @click="confirmM(item.Id, 'completed')">确认收货
                        </div>
                        <div class="order_show_selectitem">
                            <router-link v-if="isOffine" :to="{path:'/offlinemanager/offline_order_detail',query:{OrderId:item.Id}}" style="display: inline-block;width:100%;height: 100%;">
                                查看详情
                            </router-link>
                            <router-link v-else :to="{path:'/ordermanage/order_detail',query:{OrderId:item.Id}}" style="display: inline-block;width:100%;height: 100%;">
                                查看详情
                            </router-link>
                        </div>
                        <div v-if="item.Status == 'DELIVERED' && item.IsHostingOrder == true" class="order_show_selectitem">
                            <router-link :to="{path:'/ordermanage/order_hosted',query:{OrderId:item.Id}}" style="display: inline-block;width:100%;height: 100%;">
                                完成部署
                            </router-link>
                        </div>
                        <el-button slot="reference" class="order_show_button"><i class="el-icon-arrow-down"></i></el-button>
                    </el-popover>
                </div>
                <table class="order_show_table">
                    <tr>
                        <th>ID</th>
                        <th>用户名</th>
                        <th>商品ID</th>
                        <th>商品名</th>
                        <th>类型</th>
                        <th>单价</th>
                        <th>数量</th>
                        <th>优惠金额</th>
                        <th>总价</th>
                        <th>支付方式</th>
                        <th>状态</th>
                    </tr>
                    <tr>
                        <td>{{item.Id}}</td>
                        <td>{{item.Account}}</td>
                        <td>{{item.ProductId}}</td>
                        <td>{{item.ProductName}}</td>
                        <td>{{item.IsPresell?'预售':'全款'}}</td>
                        <td>{{item.ProductOriginPrice}}</td>
                        <td>{{item.Amount}}</td>
                        <td>{{item.Discount}}</td>
                        <td>
                            <div v-if="item.IsPresell">
                                <span v-if="item.PresellPayment == 'UnionPay'">{{item.TotalShouldPayPrice}}</span>
                                <span v-else-if="item.PresellPayment == 'BTC'">{{item.TotalShouldPayBtcPrice}}</span>
                                <span v-else>{{item.PresellPayment}}</span>
                            </div>
                            <div v-else>
                                <span v-if="item.Payment == 'UnionPay'">{{item.TotalShouldPayPrice}}</span>
                                <span v-else-if="item.Payment == 'BTC'">{{item.TotalShouldPayBtcPrice}}</span>
                                <span v-else>{{item.Payment}}</span>
                            </div>
                        </td>
                        <td>
                            <div v-if="item.IsPresell">
                                <span v-if="item.PresellPayment == 'UnionPay'">人民币</span>
                                <span v-else-if="item.PresellPayment == 'BTC'">BTC</span>
                                <span v-else>{{item.PresellPayment}}</span>
                            </div>
                            <div v-else>
                                <span v-if="item.Payment == 'UnionPay'">人民币</span>
                                <span v-else-if="item.Payment == 'BTC'">BTC</span>
                                <span v-else>{{item.Payment}}</span>
                            </div>
                        </td>
                        <td>
                            <span>{{item | statusFilter}}</span>
                        </td>
                    </tr>
                </table>
            </el-card>
            <!-- 分页 -->
            <el-pagination
                    background
                    :current-page="currentPage"
                    @current-change="getOrderChange"
                    :page-size="50"
                    layout="total, prev, pager, next, jumper"
                    :total="totalNo">
            </el-pagination>
        </div>
        <el-dialog
                title="修改价格"
                :visible.sync="dialogVisible"
                width="30%">
            <h4>ID</h4>
            <el-input
                    :placeholder="this.Id"
                    :disabled="true">
            </el-input>
            <h4>订单ID</h4>
            <el-input
                    :disabled="true"
                    :placeholder="this.OrderId">
            </el-input>
            <h4>人民币单价</h4>
            <el-input
                    placeholder="人民币单价"
                    v-model="input2"
                    clearable>
            </el-input>
            <h4>美元单价</h4>
            <el-input
                    placeholder="美元单价"
                    v-model="input3"
                    clearable>
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitPrice">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 确认收款弹窗 -->
        <el-dialog
                title="确认支付信息"
                :visible.sync="dialogVisibleOffline"
                width="50%">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="收款方式" prop="type">
                    <el-radio-group v-model="ruleForm.type" >
                        <el-radio label='UnionPay'>人民币</el-radio>
                        <el-radio label='USD'>美元</el-radio>
                        <el-radio label='BTC'>BTC</el-radio>
                    </el-radio-group>
                </el-form-item>
                <br>
                <div v-if="ruleForm.type == 'UnionPay'">
                    <el-form-item label="汇款人姓名" prop="bfyName">
                        <el-input v-model="ruleForm.bfyName" placeholder="请输入汇款人姓名" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="汇款银行" prop="bfyBankName">
                        <el-input v-model="ruleForm.bfyBankName" placeholder="请输入汇款银行" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="汇款卡号" prop="bankCode">
                        <el-input v-model="ruleForm.bankCode" placeholder="请输入汇款银行卡号" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="汇款金额" prop="needPay">
                        <el-input v-model.number="ruleForm.needPay" placeholder="请输入汇款金额" style="width:317px"></el-input>
                    </el-form-item>
                </div>
                <div v-if="ruleForm.type == 'USD'">
                    <el-form-item label="remitter" prop="bfyName">
                        <el-input v-model="ruleForm.bfyName" placeholder="Please fill in your remitting bank remitter's name" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remitBank" prop="bfyBankName">
                        <el-input v-model="ruleForm.bfyBankName" placeholder="Please fill in your remitting bank remitting bank name" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remittNum" prop="bankCode">
                        <el-input v-model="ruleForm.bankCode" placeholder="Please fill in your remitting bank remitting bank code" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remittAdd" prop="bankAddress">
                        <el-input v-model="ruleForm.bankAddress" placeholder="Please fill in remitting bank address" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="swiftCode" prop="swiftCode">
                        <el-input v-model="ruleForm.swiftCode" placeholder="Please fill in remitting swift code" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remitterAccount" prop="bfyAccountNumber">
                        <el-input v-model="ruleForm.bfyAccountNumber" placeholder="Please fill in remitter's account" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remitterAdd" prop="bfyAddress">
                        <el-input v-model="ruleForm.bfyAddress" placeholder="Please fill in remitt's address" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="payAmount" prop="needPay">
                        <el-input v-model.number="ruleForm.needPay" placeholder="Please fill in payment amount" style="width:317px"></el-input>
                    </el-form-item>
                </div>
                <div v-if="ruleForm.type == 'BTC'">
                    <el-form-item label="比特币地址" prop="btcAddress">
                        <el-input v-model="ruleForm.btcAddress" placeholder="" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="支付数量" prop="needPay">
                        <el-input v-model.number="ruleForm.needPay" placeholder="" style="width:317px"></el-input>
                    </el-form-item>
                </div>
                <!-- 订单状态 -->
                <el-form-item label="订单状态" prop="status" style="margin-left:-15px">
                    <el-select v-model="ruleForm.status" placeholder="请选择订单状态" style="width:306px">
                        <el-option label="待支付尾款" value="WAIT_FOR_FINAL_PAY"></el-option>
                        <el-option label="已完成支付" value="PAID"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisibleOffline = false">取 消</el-button>
                <el-button type="primary" @click="addPayInfo">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 修改地址 -->
        <el-dialog
                title="修改地址"
                :visible.sync="dialogVisible1"
                width="30%"
                style="margin-top: -9vh;">
            <h4>ID</h4>
            <el-input
                    :placeholder="this.adressInput1"
                    :disabled="true">
            </el-input>
            <h4>原收货人名字</h4>
            <el-input
                    :placeholder="this.oldAddressDetail.Receiver"
                    :disabled="true">
            </el-input>
            <h4>新收货人名字</h4>
            <el-input
                    placeholder="请输入"
                    v-model="newAddressDetail.Receiver">
            </el-input>
            <h4>原收货手机号</h4>
            <el-input
                    :placeholder="this.oldAddressDetail.MobilePhone"
                    :disabled="true">
            </el-input>
            <h4>新收货手机号</h4>
            <el-input
                    placeholder="请输入"
                    v-model="newAddressDetail.MobilePhone">
            </el-input>
            <h4>原收货电话</h4>
            <el-input
                    :placeholder="this.oldAddressDetail.PhoneNumber"
                    :disabled="true">
            </el-input>
            <h4>新收货电话</h4>
            <el-input
                    placeholder="请输入"
                    v-model="newAddressDetail.PhoneNumber">
            </el-input>
            <h4>原地址</h4>
            <el-input
                    :placeholder="this.oldAddressDetail.Address"
                    :disabled="true">
            </el-input>
            <h4>新地址</h4>
            <el-input
                    placeholder="请输入"
                    v-model="newAddressDetail.Address">
            </el-input>
            <h4>原邮编号</h4>
            <el-input
                    :placeholder="this.oldAddressDetail.ZipCode"
                    :disabled="true">
            </el-input>
            <h4>新邮编号</h4>
            <el-input
                    placeholder="请输入"
                    v-model="newAddressDetail.ZipCode">
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1 = false">取 消</el-button>
                <el-button type="primary" @click="submitAddress">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    export default {
        name:'orderComponent',
        data(){
            return{
                overTime:false,
                oldAddressDetail: {},
                order_id:'',
                orderList: [],
                currentPage: 1,
                // 修改价格弹窗所用参数
                dialogVisible: false,
                dialogVisible1: false,
                dialogVisibleOffline:false,
                OrderId:0,
                input2: '',
                input3: '',
                Id: 0,
                newAddressDetail: {
                    Receiver:'',
                    MobilePhone:'',
                    PhoneNumber:'',
                    Address:'',
                    ZipCode:''
                },
                adressInput1: '', // orderId
                ruleForm: {
                    type: 'UnionPay',
                    bfyName:'',
                    bfyBankName:'',
                    bankCode:'',
                    needPay:'',
                    bankAddress:'',
                    swiftCode:'',
                    bfyAccountNumber:'',
                    bfyAddress:'',
                    btcAddress:'',
                    status:''
                },
                rules: {
                    status: [
                        { required: true, message: '请选择支付类型', trigger: 'blur' }
                    ],
                    type: [
                        { required: true, message: '请选择支付类型', trigger: 'blur' }
                    ],
                    bfyName: [
                        { required: true, message: '请输入汇款人姓名', trigger: 'blur' }
                    ],
                    bfyBankName: [
                        { required: true, message: '请输入汇款银行', trigger: 'blur' }
                    ],
                    bankCode: [
                        { required: true, message: '请输入汇款银行卡号', trigger: 'blur' },
                        { min: 10, max: 30, message: '长度在 10 到 30 个字符', trigger: 'blur' }
                    ],
                    needPay: [
                        { required: true, message: '请输入比特币数量', trigger: 'blur' },
                        { type: 'number', message: '单价必须为数字'}
                    ],
                    bankAddress: [
                        { required: true, message: '请输入银行地址', trigger: 'blur' }
                    ],
                    swiftCode: [
                        { required: true, message: '请输入银行代码', trigger: 'blur' }
                    ],
                    bfyAccountNumber: [
                        { required: true, message: '请输入汇款人账号', trigger: 'blur' }
                    ],
                    bfyAddress: [
                        { required: true, message: '请输入汇款人地址', trigger: 'blur' }
                    ],
                    btcAddress: [
                        { required: true, message: '请输入BTC汇款地址', trigger: 'blur' },
                        { min: 10, message: '长度不低于10个字符', trigger: 'blur' },
                        { pattern: /^[A-Za-z0-9]+$/, message: '不允许输入空格汉字' }
                    ]
                }
            }
        },
        // 获取从父组件传入的数据
        props: ['totalNo', 'isOffine'],
        methods: {
            itemShow(item){
                this.$api('/admin/order/get_unpaid_order_list', {
                    account:item.Account,
                    status:'UNPAID',
                    pageNo:1,
                    pageSize:10,
                    isShowMsg: false
                }).then(res => {
                    if(res.data){
                        this.overTime = false
                    }else {
                        this.overTime = true
                    }
                });
            },
            confirmOffine(orderId){
                this.dialogVisibleOffline = true
                this.order_id = orderId
            },
            addPayInfo(){
                this.$api('/admin/offline/add_pay_info', {
                    orderId:this.order_id,
                    status: this.ruleForm.status,
                    payCurrency: this.ruleForm.type,
                    bfyName: this.ruleForm.bfyName,
                    bfyBankName: this.ruleForm.bfyBankName,
                    bankCode: this.ruleForm.bankCode,
                    bankAddress: this.ruleForm.bankAddress,
                    swiftCode: this.ruleForm.swiftCode,
                    bfyAccountNumber: this.ruleForm.bfyAccountNumber,
                    bfyAddress: this.ruleForm.bfyAddress,
                    btcAddress: this.ruleForm.btcAddress,
                    needPay: this.ruleForm.needPay,
                    remarks: ''
                }).then(res => {
                    // console.log(res)
                this.dialogVisibleOffline = false
                this.$message({
                    type: 'success',
                    message: '操作成功!'
                });
                // 刷新页面，获取修改数据，有瞬间空白页待优化
                window.location.reload()
            });
            },
            getOrderChange(val){
                let pageNo = val
                this.msg.$emit('paging',pageNo);
            },
            submitPrice(){
                if(this.input2 || this.input3){
                    this.$api('/admin/order/update_order_price', {
                        orderId: this.Id,
                        productOriginPrice: this.input2,
                        productOriginUsdPrice: this.input3,
                        notice:'修改价格成功'
                    }).then(res => {
                        // console.log(res)
                        this.dialogVisible = false
                    // 刷新页面，获取修改数据，有瞬间空白页待优化
                    setTimeout('window.location.reload()',1500)
                });
                }
            },
            submitAddress(){
                this.dialogVisible1 = false
                this.$api('/admin/order/update_order_address', {
                    orderId: this.adressInput1,
                    receiver: this.newAddressDetail.Receiver?this.newAddressDetail.Receiver:this.oldAddressDetail.Receiver,
                    addrPhone: this.newAddressDetail.PhoneNumber?this.newAddressDetail.PhoneNumber:this.oldAddressDetail.PhoneNumber,
                    addrMobilephone: this.newAddressDetail.MobilePhone?this.newAddressDetail.MobilePhone:this.oldAddressDetail.MobilePhone,
                    address: this.newAddressDetail.Address?this.newAddressDetail.Address:this.oldAddressDetail.Address,
                    zipCode: this.newAddressDetail.ZipCode?this.newAddressDetail.ZipCode:this.oldAddressDetail.ZipCode
                }).then(res => {
                this.$message({
                    type: 'success',
                    message: '地址修改成功!'
                });
                this.dialogVisible = false
                // 刷新页面，获取修改数据，有瞬间空白页待优化
                // window.location.reload()
            });
            },
            updateOrderPrice(order){
                // console.log(order)
                this.dialogVisible = true
                this.OrderId = order.OrderId
                this.Id = order.Id
                this.input2 = order.ProductOriginPrice
                this.input3 = order.ProductOriginUsdPrice
            },
            confirmM(orderId, type){
                console.log(orderId, type)
                this.$confirm('是否确认操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    // 确定收款
                    this.updateOderInfo(orderId, type)
                // this.$message({
                //     type: 'success',
                //     message: '操作成功!'
                // });
            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '取消操作'
                });
            });
            },
            updateOderInfo(orderId, type){
                // console.log(orderId, type)
                // 确认收款,取消订单，延长时间
                this.$api('/admin/order/update_order_info', {
                    orderId:orderId,
                    operationType: type,
                    notice: '操作成功'
                }).then(res => {
                window.location.reload()
            });
            },
            updateAddress(val){
                this.dialogVisible1 = true
                // console.log(val)
                this.adressId = val
                this.adressInput1 = val
                this.$api('/admin/order/get_order_detail', {
                    orderId:val
                }).then(res => {
                    // console.log(res)
                    let d = res.data.addressDetail
                    this.oldAddressDetail = d
                // console.log(this.oldAddressDetail)
            });
            }
        },
        mounted(){
            this.msg.$on('sendOrderList',(res)=>{
                this.orderList = res.orderList
                // console.log(this.orderList)
            })
        }
    }
</script>
<style lang='scss' scoped>
    @import "../assets/css/titlecom.scss";
</style>


